import { BaseConnectorConfig } from '@xip-online-data/types';
import { SourceConfig } from './types';

export function generateCollectionName(
  connectorConfig: BaseConnectorConfig,
  sinkConfig: SourceConfig,
): string {
  return `${connectorConfig.datasourceIdentifier}_${sinkConfig.queryName}`;
}

export function generateKafkaTopic(
  connectorConfig: BaseConnectorConfig,
  sinkConfig: SourceConfig,
): string {
  return `${connectorConfig.tenantIdentifier}_SOURCE_${connectorConfig.datasourceIdentifier}_${sinkConfig.queryIdentifier}`;
}
